@import 'syntax-highlighting';

@import 'bourbon/bourbon';

.cf {
  @include clearfix; }

$rs: 16px;

$font-serif: "Spoqa Han Sans", "Apple SD Gothic Neo", "Nanum Gothic", "나눔고딕", "Malgun Gothic";
$font-sans: "Spoqa Han Sans", "Apple SD Gothic Neo", "Nanum Gothic", "나눔고딕", "Malgun Gothic";


@mixin respond-to($breakpoint) {
  @media only screen and (max-width: $breakpoint + px) {
    @content; } }

// Only needed for syntax highlighting
%vertical-rhythm {
  margin-bottom: $rs; }

.pull-left {
  float: left; }
.pull-right {
  float: right; }

main {
  display: block; }

a {
  cursor: pointer; }

body {
  font-family: $font-sans;
  margin: 0;
  padding: 0;
