  -webkit-font-smoothing: antialiased;
  -webkit-text-size-adjust: none; }

.logo-readium {
  position: fixed;
  left: 10px;
  top: 10px;
  width: $rs*3;
  height: $rs*3;
  background-color: #000;
  overflow: hidden;
  z-index: 99;
  .logo {
    display: block;
    position: absolute;
    top: $rs/2;
    left: $rs/2;
    width: $rs*2;
    height: $rs*2;
    background-size: cover;
    background-repeat: no-repeat; } }

.teaserimage {
  height: 450px;
