    left: 0;
    right: 0;
    bottom: 0;
    z-index: 1;
    text-indent: -9999px; } }

.blog-header {
  width: 100%;
  max-width: 640px;
  margin: 0 auto;
  position: relative;
  padding: 0;
  @include box-sizing(border-box);
  @include respond-to(800) {
    padding: 0 $rs; }
