    background-size: cover;
    border-radius: 50%;
    z-index: 99;
    text-indent: -9999px;
    border: 3px solid #fff;
    background-color: #fff;
    -webkit-box-shadow: 0 1px 1px rgba(0,0,0,0.3);
    -moz-box-shadow: 0 1px 1px rgba(0,0,0,0.3);
    box-shadow: 0 1px 1px rgba(0,0,0,0.3); }
  .blog-title {
    margin: 0;
    padding: $rs*5.25 $rs $rs/2;
    font-size: 40px;
    text-align: center;
    font-weight: 400;
    letter-spacing: -2px;
    outline: 0;
    line-height: 50px;
    word-break: break-word;
    color: #333; }
  .blog-description {
    margin: 0 0 20px;
    padding: 0 $rs*2;
    font-size: $rs/8*9;
    line-height: 1.5;
    color: #666;
    text-align: center;
    font-weight: 400;
    letter-spacing: -1px; }
  .custom-links {
