.content {
  width: 100%;
  max-width: 640px;
  margin: 0 auto;
  word-wrap: break-word;
  word-break: break-all;
  @include box-sizing(border-box);
  @include respond-to(800) {
    padding: 0 $rs*1.5; }
  article {
    padding: 40px 0;
    border-bottom: 1px solid #f2f2f0;
    &:last-child {
      border-bottom: 0px; }
    .post-title {
      letter-spacing: -0.03em;
